a (min-width: 768px) {\n  header img.icon{\n    height: 75%;\n  }\n  header h1{\n    font-size: 5vh;\n  }\n  nav.menu .button-menu{\n    font-size: 1.7vh;\n  }\n  footer div{\n    font-size: 1.7vh ;\n  }\n  .forkme{\n    display: block;\n  }\n}\n/* Medium devices (desktops, 992px and up) */\n@media (min-width: 992px) {\n\n  header h2{\n    font-size: 2.2vh;\n  }\n  header .social-bar img{\n    height: 2.2vh;\n    width: auto;\n  }\n  .content-icon .icon{\n    height: 75%;\n  }\n  .forkme{\n    width: 17vh;\n  }\n}\n/* Large devices (large desktops, 1200px and up) */\n@media (min-width: 1200px) {\n  header ng-adsense{\n    display: block;\n  }\n  .forkme{\n    width: 19.5vh;\n  }\n}\n@media (min-width: 1600px) {\n  footer div,\n  nav.menu .button-menu{\n    font-size: 1.7vh;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"